<template>
    <div>
        <!-- -->
      <el-upload
        class="avatar-uploader"
        :action="serverUrl"
        name="img"
        :show-file-list="false"
        :on-error="uploadError"
        :before-upload="beforeUpload">
      </el-upload>

        <quill-editor 
        class="editor"
        v-model="content"
        ref="myQuillEditor" 
        :options="editorOption" 
        @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
        @change="onEditorChange($event)">
        </quill-editor>
    </div>
</template>
<script>
// 
const toolbarOptions = [
  ["bold", "italic", "underline", "strike"], //    
  ["blockquote", "code-block"], //   
  [{ header: 1 }, { header: 2 }], // 12 
  [{ list: "ordered" }, { list: "bullet" }], // 
  [{ script: "sub" }, { script: "super" }], // /
  [{ indent: "-1" }, { indent: "+1" }], // 
  // [{'direction': 'rtl'}],                         // 
  [{ size: ["small", false, "large", "huge"] }], // 
  [{ header: [1, 2, 3, 4, 5, 6, false] }], // 
  [{ color: [] }, { background: [] }], // 
  [{ font: [] }], // 
  [{ align: [] }], // 
  ["clean"], // 
  ["link", "image", "video"] // 
];

import axios from 'axios';
import { quillEditor } from "vue-quill-editor";
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import { authToken,authName } from '@/config';
import { storageHelper,getCurTime,utcTimeChange } from '@/utils';
export default {
  props: {
    /**/
    value: {
      type: String
    },
    /**/
    maxSize: {
      type: Number,
      default: 4000 //kb
    }
  },

  components: {
    quillEditor
  },

  data() {
    return {
      content: this.value,
      quillUpdateImg: false, // loadingfalse,
      editorOption: {
        placeholder: "",
        theme: "snow", // or 'bubble'
        placeholder: "",
        modules: {
          toolbar: {
            container: toolbarOptions,
            // container: "#toolbar",
            handlers: {
              image: function(value) {
                if (value) {
                  // input
                  document.querySelector(".avatar-uploader input").click();
                } else {
                  this.quill.format("image", false);
                }
              },
              // link: function(value) {
              //   if (value) {
              //     var href = prompt('url');
              //     this.quill.format("link", href);
              //   } else {
              //     this.quill.format("link", false);
              //   }
              // },
            }
          }
        }
      },
      serverUrl: "core/file/upload", // 
      token: ''
    };
  },
  mounted(){
  	this.editor.container.style.height = `${200}px`;
  	this.token = storageHelper.getItem(authToken, 'healthcare');
  },

  methods: {
    onEditorBlur() {
      //
      this.$emit("postEditorToParent",this.content);
    },
    onEditorFocus() {
      //
    },
    onEditorChange() {
      //
      //this.$emit("input", this.content);
      this.$emit("postEditorToParent",this.content);
    },

    // 
    beforeUpload(file) {
		// loading
		this.quillUpdateImg = true;
		var that =this;
		var fd = new window.FormData();
		fd.append('file', file);
		axios.post("core/file/upload", fd, {
			headers: {
				'Content-Type':'multipart/form-data',
			    token: that.token
			}
		}).then(function (res) {
			// res
			// 
			let quill = that.$refs.myQuillEditor.quill;
			// 
			if (res.code == 0) {
				// 
				let length = quill.getSelection().index;
				//   res.url
				quill.insertEmbed(length, "image", res.data.url);
				// 
				quill.setSelection(length + 1);
			} else {
				this.$message.error("");
			}
		});
		this.quillUpdateImg = false;
		return false // false
    },
    // 
    uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    }
  },
  computed: {
    editor() {
        return this.$refs.myQuillEditor.quill;
    },
}
};
</script> 

